<template>
  <div class="container">
    <div class="form">
      <el-form label-width="160px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="业务代码分类名称" prop="bizType">
          <el-select v-model="queryData.bizType" placeholder="请选择业务代码分类" filterable @change="queryChange">
            <el-option
            v-for="item in bizTypeList"
            :key="item.code"
            :value="item.code"
            :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button plain size="small" type="primary" @click="query">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tableSector" :hidden.sync="orgSectorHidden">
      <el-row :gutter="20">
        <el-col :span="24">
          <div class="grid-content bg-purple-light">
            <div class="tag">
              <el-tag
                v-for="item in currentOrg"
                :key="item.code"
                closable
                type="primary"
                @close="removeOrg(item.code)">
                {{item.name}}
              </el-tag>
              <el-button size="small" plain icon="el-icon-plus" @click="orgDialogVisible = true">新增机构</el-button>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="data">
      <div class="table">
        <el-table style="width: 100%" :data="data" stripe :header-cell-style="tableHeader" @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column prop="org" label="机构代码" min-width="120">
          </el-table-column>
          <el-table-column prop="bizNo" label="业务代码值编号" min-width="120">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.bizNo"
                        placeholder="请输入业务代码值编号">
              </el-input>
              <span v-else>{{scope.row.bizNo}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="bizName" label="业务代码值中文名称" min-width="120">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.bizName"
                        placeholder="请输入业务代码值中文名称">
              </el-input>
              <span v-else>{{scope.row.bizName}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="valDown" label="数值下限" min-width="70" v-if="valShow">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.valDown"
                        placeholder="请输入业务代码值编号">
              </el-input>
              <span v-else>{{scope.row.valDown}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="valUp" label="数值上限" min-width="70" v-if="valShow">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.valUp"
                        placeholder="请输入业务代码值中文名称">
              </el-input>
              <span v-else>{{scope.row.valUp}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="typeNo" label="业务代码分类编号" min-width="120">
          </el-table-column>
          <el-table-column prop="typeEName" label="业务代码分类英文名称" min-width="120">
          </el-table-column>
          <el-table-column prop="typeCName" label="业务代码分类中文名称" min-width="120">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="150">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="删除" placement="top">
                <el-button
                  @click.native.prevent="removeRecord(scope.row.bizNo)"
                  type="text"
                  size="small"
                  icon="iconfont icon-iconfont-shanchu"
                  style="color: #FE2B6F">
                </el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button
        :style="specialTableBtn"
        type="primary"
        icon="el-icon-plus"
        @click="add">
        新增行
      </el-button>
    </div>
    <div class="option">
      <el-button  type="primary" size="small" plain @click="edit">编辑</el-button>
      <el-button  type="primary" size="small" plain @click="save">暂存</el-button>
      <el-button  type="primary" size="small" plain @click="impDialogVisible = true">导入</el-button>
      <el-button  type="primary" size="small" plain @click="exportData">导出</el-button>
      <el-button  type="primary" size="small" plain>校验</el-button>
      <el-button  type="primary" size="small" plain>发布</el-button>
    </div>
    <el-dialog title="新增机构" :visible.sync="orgDialogVisible" width="40%">
      <el-tag
        v-for="item in allOrg"
        :key="item.code"
        :type="currentOrgCode.includes(item.code)? 'primary': 'info'"
        @click.native="addOrg(item.code)">
        {{item.name}}
      </el-tag>
    </el-dialog>
    <el-dialog title="导入文件" :visible.sync="impDialogVisible" width="40%">
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple>
        <el-button size="small" type="primary">选择文件</el-button>
        <div slot="tip" class="el-upload__tip">只能上传excel文件，且不超过500kb</div>
      </el-upload>
      <div slot="footer" style="text-align: center" class="dialog-footer">
        <el-button type="primary" plain @click="importData">导入</el-button>
        <el-button type="primary" plain @click="impDialogVisible = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {generateUUID} from '@/utils/uuid'
export default {
  name: 'BizCode',
  data () {
    return {
      queryData: {
        bizType: '01'
      },
      rules: {
        bizType: [{required: true, trigger: 'change', message: '查询条件不能为空'}]
      },
      bizTypeList: [
        {code: '01', name: '使用性质'},
        {code: '02', name: '车辆种类'},
        {code: '03', name: '商业险上年出险金额'}
      ],
      valShow: false,
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      },
      orgSectorHidden: true,
      orgDialogVisible: false,
      typeNo: '02',
      typeEName: 'carType',
      typeCName: '车辆种类',
      data: [],
      carTypeData: [
        {
          org: '50000',
          bizNo: '001',
          bizName: '客车',
          valDown: '',
          valUp: '',
          typeNo: '02',
          typeEName: 'carType',
          typeCName: '车辆种类',
          isEdit: false
        },
        {
          org: '50000',
          bizNo: '002',
          bizName: '货车',
          valDown: '',
          valUp: '',
          typeNo: '02',
          typeEName: 'carType',
          typeCName: '车辆种类',
          isEdit: false
        },
        {
          org: '50000',
          bizNo: '003',
          bizName: '挂车',
          valDown: '',
          valUp: '',
          typeNo: '02',
          typeEName: 'carType',
          typeCName: '车辆种类',
          isEdit: false
        }
      ],
      comLastAmountData: [
        {
          org: '',
          bizNo: '001',
          bizName: '0-1000',
          valDown: '0',
          valUp: '1000',
          typeNo: '03',
          typeEName: 'comLastAmount',
          typeCName: '商业险上年出险金额',
          isEdit: false
        },
        {
          org: '',
          bizNo: '002',
          bizName: '1000-2000',
          valDown: '1000',
          valUp: '2000',
          typeNo: '03',
          typeEName: 'comLastAmount',
          typeCName: '商业险上年出险金额',
          isEdit: false
        },
        {
          org: '',
          bizNo: '003',
          bizName: '2000-5000',
          valDown: '2000',
          valUp: '5000',
          typeNo: '03',
          typeEName: 'comLastAmount',
          typeCName: '商业险上年出险金额',
          isEdit: false
        }
      ],
      allOrg: [
        {code: '10000', name: '北京分公司'},
        {code: '20000', name: '上海分公司'},
        {code: '30000', name: '江苏省分公司'},
        {code: '40000', name: '山东省分公司'},
        {code: '50000', name: '山西省分公司'}
      ],
      currentOrgCode: ['40000', '50000'],
      multipleSelection: [],
      impDialogVisible: false,
      useProData: [
        {
          org: '',
          bizNo: '001',
          bizName: '家庭自用',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: false
        },
        {
          org: '',
          bizNo: '002',
          bizName: '营业出租租赁',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: false
        },
        {
          org: '',
          bizNo: '003',
          bizName: '营业公路客运',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: false
        },
        {
          org: '',
          bizNo: '004',
          bizName: '营业城市公交',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: false
        },
        {
          org: '',
          bizNo: '005',
          bizName: '营业旅游',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: false
        }
      ]
    }
  },
  methods: {
    queryChange (val) {
      if (val === '02') {
        this.data = this.carTypeData
        this.valShow = false
        this.orgSectorHidden = false
      } else if (val === '03') {
        this.data = this.comLastAmountData
        this.valShow = true
        this.orgSectorHidden = true
      } else {
        this.data = []
        this.valShow = false
        this.orgSectorHidden = true
      }
    },
    query () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          console.log(this.queryData.bizType)
          if (this.queryData.bizType === '02') {
            this.data = this.carTypeData
            this.valShow = false
            this.orgSectorHidden = false
          } else if (this.queryData.bizType === '03') {
            this.data = this.comLastAmountData
            this.valShow = true
            this.orgSectorHidden = true
          } else {
            this.data = []
            this.valShow = false
            this.orgSectorHidden = true
          }
        }
      })
    },
    handleSelectionChange (vals) {
      this.multipleSelection = []
      vals.forEach(val => {
        this.multipleSelection.push(val.bizNo)
      })
    },
    edit () {
      if (this.multipleSelection.length > 0) {
        this.data.forEach(item => {
          if (this.multipleSelection.includes(item.bizNo)) {
            item.isEdit = true
          }
        })
      }
    },
    save () {
      this.data.forEach(item => {
        item.isEdit = false
      })
    },
    sure () {
    },
    cancel () {
    },
    add () {
      const no = generateUUID()
      console.log(no)
      if (this.queryData.bizType === '01') {
        const newItem = {
          org: '',
          bizNo: '',
          bizName: '',
          valDown: '',
          valUp: '',
          typeNo: '01',
          typeEName: 'usePro',
          typeCName: '使用性质',
          isEdit: true
        }
        this.data.push(newItem)
      } else if (this.queryData.bizType === '02') {
        const newItem = {
          org: '',
          bizNo: '',
          bizName: '',
          valDown: '',
          valUp: '',
          typeNo: '02',
          typeEName: 'carType',
          typeCName: '车辆种类',
          isEdit: true
        }
        this.data.push(newItem)
      } else if (this.queryData.bizType === '03') {
        const newItem = {
          org: '',
          bizNo: '',
          bizName: '',
          valDown: '',
          valUp: '',
          typeNo: '03',
          typeEName: 'comLastAmount',
          typeCName: '商业险上年出险金额',
          isEdit: true
        }
        this.data.push(newItem)
      } else {
      }
    },
    removeOrg (code) {
      const index = this.currentOrgCode.indexOf(code)
      this.currentDimensionData.splice(index, 1)
    },
    addOrg (code) {
      if (!this.currentOrgCode.includes(code)) {
        this.currentOrgCode.push(code)
      }
    },
    removeRecord (bizNo) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.data.findIndex(item => item.bizNo === bizNo)
        this.data.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    },
    importData () {
      this.impDialogVisible = false
      this.data = this.useProData
      this.valShow = false
      this.orgSectorHidden = true
    },
    exportData () {
      this.$confirm('确定要导出数据吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'info'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '导出成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消导出'
        })
      })
    }

  },
  computed: {
    currentOrg () {
      return this.currentOrgCode.map(code => {
        return this.allOrg.find(item => item.code === code)
      })
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>
  .tableSector{
    background: #fff;
    margin: 10px auto;
  }
  .tag{
    display: block;
    margin-bottom: 16px;
  }

  .el-tag{
    margin-left: 10px;
    margin-bottom: 5px;
  }
</style>



